<template>
  <div class="content-home">
    <headerUsually btnShow="false" titleText="个人中心" url></headerUsually>
    <div class="content-userInfo" :style="{backgroundImage:'url(' + bgimgSrc+ ')'}">
      <div class="userInfo">
        <span class="unseName">{{unseName}}</span>
        <span class="dividingLine"></span>
        <span class="useClass">{{shiftDescript}}</span>
      </div>
      <div class="site-name">站点：{{workStationCode}}</div>
    </div>
    <div class="list">
      <div class="item" @click="gotoFun(item)" v-for="(item,index) in list" :key="index">
        <div class="text">{{item.text}}</div>
        <div class="iconfont icon-jiantou1"></div>
      </div>
    </div>
    <div class="btn" @click="exitSystemFun()">退出登录</div>

    <mainFooter activeItem="3"></mainFooter>
  </div>
</template>

<script>
import { Toast, MessageBox } from "mint-ui";
import ajaxGet from "@/conf/ajaxGet";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import mainFooter from "@/components/common/mobileOrdering/mainFooter.vue";
export default {
  components: {
    headerUsually,
    mainFooter
  },
  data() {
    return {
      list: [
        { text: "密码修改", url: "/mobileOrdering/changePassword" },
        { text: "打印设置", url: "/mobileOrdering/printSet" },
        { text: "菜谱更新", url: "post", features: "updateRecipe" },
        { text: "网络测试", url: "/mobileOrdering/networkCheck" },
        { text: "版本信息", url: "/mobileOrdering/versionInfo" }
      ],
      unseName: "",
      shiftDescript: "",
      workStationCode: "",
      bgimgSrc: "./static/images/mobileOrdering/home2x.png"
    };
  },
  created() {
    let dcgateway = localStorage.getItem("dcgateway")
      ? JSON.parse(localStorage.getItem("dcgateway"))
      : {};
    this.unseName = dcgateway.userInfo.userName;
    this.shiftDescript = dcgateway.userInfo.shiftDescript;
    this.workStationCode = dcgateway.userInfo.workStationCode;
  },
  methods: {
    exitSystemFun() {
      this.$router.push("/mobileOrdering/login");
    },
    async gotoFun(item) {
      if (item.url.indexOf("/mobileOrdering/") > -1) {
        this.$router.push(item.url);
      } else {
        console.log("ajax:", this.axios);
        if (item.features == "updateRecipe") {
          this.getDishInfo();
        }
      }
    },
    async getDishInfo() {
      let res = await ajaxGet("/pos/router?method=getCachePluDatas", {
        timeout: 3 * 60 * 1000
      });
      if (res && res.resultCode == 0) {
        let dcDishInfo = res.data;
        this.$store.commit("submitDishInfo", res.data);
        localStorage.setItem("dcDishInfo", JSON.stringify(dcDishInfo));
        this.msg("更新完成！");
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less" scoped>
.content-home {
  padding-top: 46px;
  .content-userInfo {
    padding-top: 30px;
    width: 100%;
    height: 115px;
    background-repeat: no-repeat;
    background-size: 100%;
    box-sizing: border-box;

    .userInfo {
      padding-left: 20px;
      color: #c4ad68;
      font-size: 24px;
      display: flex;
      align-items: center;
      .dividingLine {
        display: inline-block;
        margin: 0 15px;
        height: 20px;
        width: 2px;
        background-color: #c4ad68;
      }
    }
    .site-name {
      font-size: 16px;
      color: #c4ad68;
      text-align: left;
      padding-left: 20px;
    }
  }
  .list {
    background: #ffffff;
    padding-left: 15px;
    .item {
      padding: 17px 0;
      border-bottom: 1px solid #ddd;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .text {
        font-size: 14px;
        color: #000000;
      }
      .iconfont {
        margin-right: 17px;
        color: #cccccc;
      }
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



