<template>
  <div class="content-footer">
    <div class="item" v-for="(item, index) in list" :key="index" @click="gotoFun(item)">
      <div class="iconfont" :class="item.icon" v-if="index==activeItem? false:true"></div>
      <div class="iconfont" :class="item.activeIcon" v-if="index==activeItem? true:false"></div>
      <div class="text">{{item.text}}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeItem: String
  },
  data() {
    return {
      list: [
        {
          text: "餐位图",
          icon: "icon-chuangwei11",
          activeIcon: "icon-chuangwei2",
          url: "/mobileOrdering/index"
        },
        {
          text: "预订单",
          icon: "icon-dingdan21",
          activeIcon: "icon-dingdan11",
          url: "/mobileOrdering/preOrder"
        },
        {
          text: "账单",
          icon: "icon-tongji",
          activeIcon: "icon-tongji1",
          url: "/mobileOrdering/bill"
        },
        // {
        //   text: "设置",
        //   icon: "icon-shezhi21",
        //   activeIcon: "icon-shezhi4",
        //   url: "/mobileOrdering/set"
        // },
        {
          text: "个人中心",
          icon: "icon-gerenzhongxin2",
          activeIcon: "icon-gerenzhongxin11",
          url: "/mobileOrdering/home"
        }
      ]
    };
  },
  mounted() {},
  methods: {
    gotoFun(item) {
      if (item.url == this.$route.path) {
        return;
      }
      this.$router.push(item.url);
    }
  }
};
</script>
<style lang="less">
.content-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  background: #393c41;
  .item {
    width: 25%;
    .text {
      font-size: 10px;
      color: #ffffff;
      text-align: center;
    }
    .iconfont {
      color: #fff;
      font-size: 24px;
      margin-bottom: -5px;
    }
  }
}
</style>




